import React from "react";
import s from "./AccentButton.module.sass";

const AccentButton = ({
  onClick,
  children,
  backgroundColor,
  width,
  outline,
  classStyle,
}) => {
  return (
    <a
      onClick={onClick}
      className={[classStyle, s.AccentButton, width].join(" ")}
      style={
        outline
          ? { border: "3px solid #6346E6", background: "#ffffff" }
          : { background: backgroundColor, color: "#ffffff" }
      }
    >
      {children}
    </a>
  );
};

export default AccentButton;
