import React from "react";
import logo from "../../../img/logo.svg";

import s from './Logo.module.sass'


const Logo = ({size}) => {
    return (
        <img src={logo} alt="SBERBANK"
             style={size === "normal" ? {width: "306px", height: "37px"} : {}} className={size === "small" ? s.small : ""}
        />
    );
}

export default Logo;
