import React, { useEffect } from "react";
import s from "./TestResult.module.sass";
import TestImage from "../../../img/german-c.png";
import ResultImage from "../../../img/background/ResultImage.png";
import resultAccent from "../../../img/resultAccent.svg";

import EmojBad from "../../../img/background/result/bad.svg";
import EmojOk from "../../../img/background/result/good.svg";
import EmojNice from "../../../img/background/result/ok.svg";

import sberCat from "../../../img/sberCat.svg";

import Header from "../../shared/Navigation/Header/Header";
import AccentButton from "../../UI/Buttons/AccentButton/AccentButton";
import { AccentButtonWidth } from "../../UI/Buttons/ButtonWidth/ButtonWidth";
import { AccentButtonColors } from "../../UI/Buttons/AccentButton/AccentButtonColors";
import bridge from "@vkontakte/vk-bridge";
import { Api } from "../../../api/api";
const MyApi = new Api();
const TestResult = ({
  id,
  allAnswer,
  setActivePanel,
  result,
  setResultTest,

  results,
  wallpaper,
  activeTestKey,
}) => {
  useEffect(async () => {
    let profile = await MyApi.getProfile();
    if (result >= profile.progress[activeTestKey]) {
      profile.progress[activeTestKey] = 1;
      if (result == 0) {
        profile.progress[activeTestKey] == 1;
      }
      console.log("set", profile.progress);
      await MyApi.update({ progress: profile.progress });
    }
    console.log("сохранили", profile.progress);
  }, []);

  return (
    <>
      <div className={s.TestResult}>
        <img src={ResultImage} alt="" className={s.BottomImage} />
        <img
          src={result <= 2 ? EmojBad : result <= 4 ? EmojOk : EmojNice}
          alt=""
          className={s.topImage}
        />
        <Header
          closeButton={true}
          go={() => {
            setActivePanel("home");
          }}
          nonLogo={true}
        />
        <img src={TestImage} alt="" className={s.BackgroundImage} />
        <div className={s.Result}>
          <p>
            {result} / {allAnswer}
          </p>
        </div>
        <div className={s.TestResultContentWrapper}>
          <div className={s.TestResultTitle}>
            <p className={s.TestResultTitleTitle}>
              {result <= 2
                ? results[0].title
                : result <= 4
                ? results[1].title
                : results[2].title}
            </p>
            <p className={s.TestResultTitleText}>
              {result <= 2
                ? results[0].description
                : result <= 4
                ? results[1].description
                : results[2].description}
            </p>
          </div>
          <div className={s.BottomContent}>
            <div
              className={s.giftWrapper}
              style={{
                opacity: wallpaper ? 1 : 0,
              }}
            >
              <p className={s.giftTitle}>Получи подарок от СберКота! </p>
              <p className={s.giftText}>
                Просто нажми на меня и забери обои на свой смартфон
              </p>
              <img src={resultAccent} alt="" className={s.resultAccent} />
              {window.location.href.includes("vk_platform=mobile") ? (
                <img
                  src={sberCat}
                  alt=""
                  className={s.sberCat}
                  onClick={() => {
                    if (wallpaper) {
                      bridge
                        .send("VKWebAppDownloadFile", {
                          url: wallpaper.url,
                          filename: wallpaper.name,
                        })
                        .then((data) => {
                          if (data.result) {
                            console.log(data.result);
                          } else {
                            console.log("error");
                          }
                        })
                        .catch((error) => {
                          console.log(error);
                        });
                    }
                  }}
                />
              ) : (
                <>
                  {wallpaper?.url && (
                    <a target="_blank" href={wallpaper.url} download>
                      <img src={sberCat} alt="" className={s.sberCat} />
                    </a>
                  )}
                </>
              )}
            </div>

            <div className={s.Buttons}>
              <AccentButton
                width={AccentButtonWidth.XL}
                outline={true}
                onClick={() => {
                  // TODO: ссылка на историю
                  bridge.send("VKWebAppShowStoryBox", {
                    background_type: "image",
                    url: "https://i.ibb.co/c2k9KfH/1080x1920.png",
                    attachment: {
                      text: "learn_more",
                      type: "url",
                      url: "https://practicum.sberclass.ru/",
                    },
                  });
                }}
              >
                <p className={s.OutlineButton}>Поделиться</p>
              </AccentButton>
              <div className={s.ButtonWrapper}>
                <AccentButton
                  width={AccentButtonWidth.XL}
                  backgroundColor={AccentButtonColors.PURPLE}
                  onClick={() => {
                    const MyApi = new Api();
                    MyApi.getProfile().then((data) => {
                      if (data.isMessageAllowed) {
                        setActivePanel("home");
                      } else {
                        setActivePanel("subscribe");
                      }
                    });
                  }}
                >
                  <p className={s.Button}>В меню</p>
                </AccentButton>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default TestResult;
