import React, { useEffect, useState } from "react";
import PropTypes, { number } from "prop-types";
import profile from "../../../img/background/profile.png";
import avatar from "../../../img/background/avatar.svg";
import s from "./Profile.module.sass";

import Grid from "../../Grid/Grid/Grid";
import SharedSvgSelector from "../../shared/SvgSelector/SvgSelector";
import Footer from "../../shared/Navigation/Footer/Footer";
import { AccentButtonColors } from "../../UI/Buttons/AccentButton/AccentButtonColors";
import { AccentButtonWidth } from "../../UI/Buttons/ButtonWidth/ButtonWidth";
import AccentButton from "../../UI/Buttons/AccentButton/AccentButton";
import { Api } from "../../../api/api";

const MyApi = new Api();
const Profile = ({ id, fetchedUser, activePanel, setActivePanel }) => {
  const [data, setData] = useState(["0", "0"]);
  const [progress, setProgress] = useState([0, 0, 0, 0, 0, 0, 0]);
  useEffect(() => {
    MyApi.getProfile().then(function (r) {
      setProgress(r.progress);
    });
  }, []);

  const [statView, setStatView] = useState(false);

  let percent = 100;
  let completeCount = 0;
  for (let i in progress) {
    if (progress[i] >= 1) {
      completeCount += 1;
    }
    percent -= ((progress[i] / 5) * 100) / 7;
  }
  if (percent <= 1) {
    percent = 1;
  }
  percent = Math.round(percent);

  return (
    <>
      <div className={s.Profile}>
        <img src={profile} alt="" className={s.BackgroundImage} />
        <div>
          <Grid>
            <div className={s.AvatarWrapper}>
              <img src={fetchedUser.photo_200} alt="" className={s.Avatar} />
            </div>
            <div className={s.Title}>
              {fetchedUser.first_name}, <br />
              твой профиль
            </div>
            <a
              className={s.LinkRate}
              onClick={() => {
                setStatView(true);
              }}
            >
              Узнать свой рейтинг
              <SharedSvgSelector
                id={"veryLongArrow"}
                width={"19"}
                height={"7"}
                fillColor={"#252041"}
              />
            </a>
            <div className={s.Info}>
              {statView ? (
                <div className={s.Stat}>
                  <p>
                    По числу правильных тестов, ты находишься в ТОП-
                    {percent}%
                  </p>
                  <a
                    onClick={() => {
                      setStatView(false);
                    }}
                  >
                    <SharedSvgSelector
                      id={"veryLongArrow"}
                      width={"19"}
                      height={"7"}
                      fillColor={"#ffffff"}
                    />
                    Вернуться
                  </a>
                </div>
              ) : (
                <div className={s.Border}>
                  {progress.every((item) => item == 0) ? (
                    <p className={s.InfoText}>
                      Вы не прошли <br />
                      ни одного теста!
                    </p>
                  ) : (
                    <p className={s.InfoText}>
                      Молодец, ты <br />
                      прошёл уже {completeCount} тестов
                    </p>
                  )}
                </div>
              )}
            </div>
            <AccentButton
              onClick={() => {
                setActivePanel("save");
              }}
              backgroundColor={AccentButtonColors.PURPLE}
              width={AccentButtonWidth.XL}
            >
              <div className={s.Button}>
                <div className={s.ButtonIconWrapper}>
                  <SharedSvgSelector
                    id={"play"}
                    width={"19"}
                    height={"21"}
                    fillColor={"#ffffff"}
                  />
                </div>
                Cмотреть все видеоролики
              </div>
            </AccentButton>
          </Grid>
        </div>
      </div>
      <Footer activePanel={activePanel} setActivePanel={setActivePanel} />
    </>
  );
};

Profile.propTypes = {
  id: PropTypes.string.isRequired,
  fetchedUser: PropTypes.shape({
    photo_200: PropTypes.string,
    first_name: PropTypes.string,
    last_name: PropTypes.string,
    city: PropTypes.shape({
      title: PropTypes.string,
    }),
  }),
};

export default Profile;
