import React, { useState, useEffect } from "react";
import bridge from "@vkontakte/vk-bridge";
import {
  ScreenSpinner,
  AdaptivityProvider,
  AppRoot,
  ConfigProvider,
  View,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";

import german from "./img/german-c.png";
import Home from "./components/panels/Home/Home";
import Profile from "./components/panels/Profile/Profile";
import TestMain from "./components/Test/Main/TestMain";
import TestVideo from "./components/Test/Video/TestVideo";
import SuperGame from "./components/panels/SuperGame/SuperGame";
import SaveVideo from "./components/panels/SaveVideo/SaveVideo";
import TestResult from "./components/Test/Result/TestResult";
import TestTask from "./components/Test/Task/TestTask";
import OnBoard from "./components/Test/onBoard/onBoard";
import Subscribe from "./components/Test/Subscribe/Subscribe";
import Failed from "./components/panels/SuperGame/Result/Failed/Failed";
import Win from "./components/panels/SuperGame/Result/Win/Win";
import { Api } from "./api/api";
import { InputData } from "./tests";
import styled from "@emotion/styled";

const MyApi = new Api();
bridge.send("VKWebAppInit");
bridge.subscribe((e) => {
  console.log(e);
});
const App = () => {
  const [activePanel, setActivePanel] = useState("home");
  const [testData, setTestData] = useState(InputData[0]);

  const [resultTest, setResultTest] = useState(0);
  const [fetchedUser, setUser] = useState(null);
  const [popout, setPopout] = useState(<ScreenSpinner size="large" />);

  useEffect(() => {
    MyApi.first().then(function (response) {
      console.log(response);
      setActivePanel(response === true ? "onBoard" : "home");
    });

    MyApi.getProfile().then(function (response) {
      console.log(response);
    });

    async function fetchData() {
      const user = await bridge.send("VKWebAppGetUserInfo");

      setUser(user);
      setPopout(null);
    }

    fetchData();
  }, []);

  const go = (e) => {
    setActivePanel(e.currentTarget.dataset.to);
  };

  return (
    <ConfigProvider>
      <AdaptivityProvider>
        <AppRoot popout={popout}>
          <View activePanel={activePanel}>
            <Home
              id={"home"}
              fetchedUser={fetchedUser}
              activePanel={activePanel}
              setActivePanel={setActivePanel}
              setTestData={setTestData}
            />
            <Profile
              id={"profile"}
              fetchedUser={fetchedUser}
              activePanel={activePanel}
              setActivePanel={setActivePanel}
            />
            <TestMain
              setResultTest={setResultTest}
              id={"test"}
              setActivePanel={setActivePanel}
              allQuestion={5}
              title={testData.title}
              description={testData.description}
              img={testData.cover}
            />
            <TestVideo
              id={"video"}
              setActivePanel={setActivePanel}
              video={testData.video}
            />
            <SuperGame
              id={"super"}
              fetchedUser={fetchedUser}
              activePanel={activePanel}
              setActivePanel={setActivePanel}
            />
            <SaveVideo
              id={"save"}
              activePanel={activePanel}
              setActivePanel={setActivePanel}
            />
            <TestResult
              id={"testResult"}
              result={resultTest}
              activeTestKey={InputData.indexOf(
                InputData.find((item) => item.title === testData.title)
              )}
              results={testData.results}
              wallpaper={testData.wallpaper}
              allAnswer={5}
              setActivePanel={setActivePanel}
              setResultTest={setResultTest}
            />
            {testData.question.map((data, index) => {
              return (
                <TestTask
                  id={"testPanel" + (index + 1)}
                  setActivePanel={setActivePanel}
                  count={index + 1}
                  TestImage={german}
                  title={data.title}
                  type={data.type}
                  background={testData.background}
                  answers={data.content}
                  answer={data.answer}
                  setResultTest={setResultTest}
                  tr={() => {
                    if (index + 1 === testData.question.length) {
                      setActivePanel("testResult");
                    } else {
                      setActivePanel("testPanel" + (index + 2));
                    }
                  }}
                />
              );
            })}

            <Subscribe
              id={"subscribe"}
              setActivePanel={setActivePanel}
              activePanel={activePanel}
            />
            <Failed
              id={"superGameFailed"}
              setActivePanel={setActivePanel}
              activePanel={activePanel}
            />
            <Win
              id={"superGameWin"}
              setActivePanel={setActivePanel}
              activePanel={activePanel}
            />
            <OnBoard
              id={"onBoard"}
              setActivePanel={setActivePanel}
              activePanel={activePanel}
            />
          </View>
        </AppRoot>
      </AdaptivityProvider>
    </ConfigProvider>
  );
};

export default App;
