import React, { useEffect, useState } from "react";
import PropTypes, { array } from "prop-types";
import s from "./Home.module.sass";
import background from "../../../img/background/menu.png";

import Header from "../../shared/Navigation/Header/Header";
import Grid from "../../Grid/Grid/Grid";

import Card from "../../Test/Card/Card";

import german from "../../../img/german-c.png";
import SuperCardTest from "../../../img/icons/super.png";
import SuperCard from "../../Test/SuperCard/SuperCard";

import Footer from "../../shared/Navigation/Footer/Footer";
import { Api } from "../../../api/api";

import p from "../../../img/home/1.png";
import ph from "../../../img/home/2.png";
import pho from "../../../img/home/3.png";
import photo from "../../../img/home/4.png";
import { shuffleArray } from "@vkontakte/vkjs";
import lock from "../../../img/icons/close.png";
import complete from "../../../img/icons/complete.png";
import superLock from "../../../img/icons/super_lock.png";
import last1 from "../../../img/postbord/last1.png";
import last2 from "../../../img/postbord/last2.png";
import last3 from "../../../img/postbord/last3.png";

import { InputData } from "../../../tests";

const MyApi = new Api();
const photos = [p, ph, pho, photo];
const photosRandom = [p, ph, pho, photo, p, ph, pho];

const Home = ({
  id,
  fetchedUser,
  activePanel,
  setActivePanel,
  setTestData,
}) => {
  const [data, setData] = useState(InputData);
  const [level, setLevel] = useState(0);
  const [progress, setProgress] = useState([0, 0, 0, 0, 0, 0, 0]);
  const [completeStatus, setCompleteStatus] = useState(false);
  useEffect(() => {
    MyApi.getProfile().then(function (r) {
      setProgress(r.progress);
    });

    MyApi.getProfile().then(function (r) {
      setCompleteStatus(r.complete);
    });
  }, []);
  return (
    <>
      <div className={s.Home}>
        <Header />
        <img src={background} alt={""} className={s.BackgroundImage} />

        {completeStatus ? (
          <div
            style={{
              height: "auto",
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              marginTop: "125px",
              gap: "12px",
              zIndex: "99",
            }}
          >
            <img src={last1} width={343 * 0.9} height={248 * 0.9} />
            <img
              src={last2}
              width={343 * 0.9}
              height={259 * 0.9}
              onClick={() => {
                let a = document.createElement("a");
                Object.assign(a, {
                  href: "https://sber-z.sberclass.ru",
                  target: "_blank",
                });
                a.click();
              }}
            />
            <img
              src={last3}
              width={343 * 0.9}
              height={259 * 0.9}
              onClick={() => {
                let a = document.createElement("a");
                Object.assign(a, {
                  href: "https://sberstudent.sberclass.ru",
                  target: "_blank",
                });
                a.click();
              }}
            />
          </div>
        ) : (
          <>
            <Grid>
              <div>
                <div className={s.HomeTitle}>
                  <p>Практикум</p>
                  <div className={s.HomeTitleAccent}>
                    <p>по предпринимательству</p>
                  </div>
                </div>
                <div></div>
              </div>
              {data.map((d, key) => (
                <Card
                  allQuestion={5}
                  title={d.title}
                  img={progress[key] >= 1 ? complete : photosRandom[key]}
                  arrow={progress[key] >= 1 ? false : true}
                  onClick={() => {
                    setActivePanel("test");
                    setTestData(d);
                  }}
                  descriptoin={key !== 0 ? "Тест #" + key : "Интро"}
                  CardId={d.id}
                  key={key}
                  dataGet={setTestData}
                  setActivePanel={setActivePanel}
                />
              ))}

              <SuperCard
                accept={progress.every((el) => el >= 1) ? true : false}
                img={
                  progress.every((el) => el >= 1) ? SuperCardTest : superLock
                }
                onClick={() => {
                  if (progress.every((el) => el >= 1)) {
                    setActivePanel("super");
                  }
                }}
              />
            </Grid>
          </>
        )}
      </div>
      <Footer activePanel={activePanel} setActivePanel={setActivePanel} />
    </>
  );
};

Home.propTypes = {
  id: PropTypes.string.isRequired,
  fetchedUser: PropTypes.shape({
    photo_200: PropTypes.string,
    first_name: PropTypes.string,
    last_name: PropTypes.string,
    city: PropTypes.shape({
      title: PropTypes.string,
    }),
  }),
};

export default Home;
