import React from "react";
import s from "./SuperCard.module.sass";
import SharedSvgSelector from "../../shared/SvgSelector/SvgSelector";
import AccentButton from "../../UI/Buttons/AccentButton/AccentButton";
import { AccentButtonColors } from "../../UI/Buttons/AccentButton/AccentButtonColors";
import { AccentButtonWidth } from "../../UI/Buttons/ButtonWidth/ButtonWidth";
import lock from "./lock.png";

const SuperCard = ({ accept, img, onClick }) => {
  return (
    <div className={s.SuperCard} onClick={onClick}>
      <p className={s.CardTitle}>
        {!accept && <img src={lock} width={18} height={18} />}
        Суперигра
      </p>
      {!accept ? (
        <p className={s.CardSubTitle}>
          Пройди все тесты и доступ откроется. Не упусти возможность получить
          суперприз
        </p>
      ) : (
        <p className={s.CardSubTitle}>
          Ура, ты молодец и прошел все тесты! Переходи к финальному заданию и
          забирай главный приз
        </p>
      )}
      <div
        className={s.CardImage}
        style={{ marginBottom: accept ? "15px" : "25px" }}
      >
        <img src={img} alt="" />
      </div>
      {accept ? (
        <div className={s.ButtonWrapper}>
          <AccentButton
            width={AccentButtonWidth.MAX}
            backgroundColor={AccentButtonColors.PURPLE}
            onClick={() => {}}
          >
            <p className={s.Button}>Я готов!</p>
          </AccentButton>
        </div>
      ) : (
        ""
      )}
    </div>
  );
};

export default SuperCard;
