import React from "react";
import s from "./Footer.module.sass";

import SharedSvgSelector from "../../SvgSelector/SvgSelector";

const Footer = ({ activePanel, setActivePanel, blurNon }) => {
  return (
    <>
      {blurNon ? "" : <div className={s.Blur}></div>}
      <nav className={s.Footer}>
        <div
          className={s.IconButton}
          onClick={() => {
            setActivePanel("home");
          }}
        >
          <SharedSvgSelector
            id={"home"}
            height={32}
            width={30}
            fillColor={activePanel === "home" ? "#6346E6" : "#E8ECEF"}
          />
        </div>
        <div className={s.Separator}></div>
        <div
          className={s.IconButton}
          onClick={() => {
            setActivePanel("profile");
          }}
        >
          <SharedSvgSelector
            id={"profile"}
            height={30}
            width={30}
            fillColor={activePanel === "profile" ? "#6346E6" : "#E8ECEF"}
          />
        </div>
      </nav>
    </>
  );
};

export default Footer;
