import React from "react";
import s from "./Header.module.sass";
import Logo from "../../Logo/Logo";
import Item from "../../../Grid/Item/Item";
import PrimaryButton from "../../../UI/Buttons/PrimaryButton/PrimaryButton";
import SharedSvgSelector from "../../SvgSelector/SvgSelector";

const Header = ({
  backButton,
  closeButton,
  go,
  testCounter,
  count,
  nonLogo,
}) => {
  return (
    <header
      className={s.Header}
      style={{ top: backButton || closeButton ? "35px" : "40px" }}
    >
      {backButton || closeButton ? (
        <PrimaryButton onClick={go}>
          <SharedSvgSelector
            id={closeButton ? "exit" : "arrow"}
            width={"32"}
            height={"32"}
            fillColor={"#2A2B2D"}
          />
        </PrimaryButton>
      ) : (
        ""
      )}
      {testCounter ? (
        <div className={s.TestCounter}>
          <p>{count}</p>
        </div>
      ) : nonLogo ? (
        ""
      ) : (
        <div
          style={{ width: backButton ? "282px" : "100%" }}
          className={s.Logo}
        >
          <Logo size={backButton ? "small" : "normal"} />
        </div>
      )}
    </header>
  );
};

export default Header;
