import React, { useEffect, useState } from "react";
import s from "./TestTask.module.sass";
import Header from "../../shared/Navigation/Header/Header";
import styled from "@emotion/styled";

const colors = [
  "linear-gradient(180deg, #4A25DD 0%, #8D7FF5 100%)",
  "linear-gradient(180deg, #FB8B3B 0%, #FFC452 100%)",
  "linear-gradient(180deg, #D12783 0%, #FF89AC 100%)",
  "linear-gradient(180deg, #068DAD 0%, #94E3AD 100%)",
];

const ImageItem = styled.img`
  border-radius: 20px;
  width: 100%;
`;

const StyledInput = styled.input`
  border-radius: 15px;
  background: linear-gradient(180deg, #4a25dd 0%, #8d7ff5 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-size: 30px;
  line-height: 29px;
  letter-spacing: 18px;
  text-align: start;

  margin-left: 1px;
  border: 0px;
  justify-content: center;
  align-items: center;
  text-align: start;
  width: 100%;
  height: 80%;
`;
const BGInput = styled.div`
  background: #d7d7d7;
  padding: 12px;
  border-radius: 15px;
  width: 300px;
  height: 54px;
  margin-top: 20px;
`;

const TestTask = ({
  id,
  setActivePanel,
  questionType,
  setResultTest,
  answers,
  tr,
  title,
  TestImage,
  count,
  answer,
  type,
  background,
}) => {
  useEffect(() => {
    if (document.querySelector("input")) {
      document.querySelector("input").addEventListener("input", function () {
        this.value = this.value.toUpperCase();
        if (this.value.length >= 8) {
          if (this.value == answer) {
            setResultTest((item) => item + 1);
          }
          this.value = "";
          tr();
        }
      });
    }
  }, []);

  const [startIndex, setStartIndex] = useState(Math.floor(Math.random() * 4));
  return (
    <>
      <div className={s.TestTask}>
        <Header
          backButton={true}
          go={() => {
            setActivePanel("home");
          }}
          testCounter={true}
          count={count + "/5"}
        />
        <div
          style={{ background: background }}
          alt=""
          className={s.BackgroundImage}
        />
        <div className={s.TestQuestion}>
          <p>{title}</p>
        </div>
        <p className={s.TestQuestionSubTitle}>Выбери правильный ответ</p>
        {type === "secret" && (
          <BGInput>
            <StyledInput type="text" maxlength={8}></StyledInput>
            <div
              style={{
                display: "flex",
                gap: "22px",
                marginLeft: "6px",
                marginTop: "-6px",
              }}
            >
              {[0, 0, 0, 0, 0, 0, 0, 0].map(() => {
                return (
                  <div
                    style={{
                      margin: "5px 0",
                      height: "2px",
                      width: "16px",
                      background: "white",
                    }}
                  />
                );
              })}
            </div>
          </BGInput>
        )}

        {type === "text" ? (
          <div className={s.adaptiveAnswers}>
            {answers.map((a, index) => {
              index += startIndex;
              if (index > colors.length) {
                index = index - colors.length - 1;
              }
              return (
                <div
                  style={{
                    background:
                      "linear-gradient(180deg, #4A25DD 0%, #8D7FF5 100%)",
                  }}
                  className={s.answer}
                  onClick={() => {
                    if (
                      answers.indexOf(answers.find((item) => item == a)) ===
                      answer
                    )
                      setResultTest((item) => item + 1);

                    tr();
                  }}
                >
                  <p>{a}</p>
                </div>
              );
            })}
          </div>
        ) : type === "photo" || type === "secret" ? (
          <div
            style={{
              display: "grid",
              height: "100%",
              width: "calc(100vw - 18px)",
              gridTemplateColumns: "1fr 1fr",
              maxHeight: "600px",
              maxWidth: "600px",
              justifyContent: "center",
              alignItems: "center",
              marginTop: "50px",
              gap: "25px",
            }}
          >
            {answers.map((a, index) => {
              return (
                <ImageItem
                  src={a.url}
                  onClick={() => {
                    if (
                      answers.indexOf(answers.find((item) => item == a)) ===
                      answer
                    ) {
                      setResultTest((item) => item + 1);
                    }

                    tr();
                  }}
                />
              );
            })}
          </div>
        ) : (
          ""
        )}
      </div>
    </>
  );
};

export default React.memo(TestTask);
