import axios from "axios";
import bridge from "@vkontakte/vk-bridge";

/**
 * Формат данных:
 * {
 *      isFirstTimeOpened: boolean
 *      progress: [{
 *          // ID теста
            id: number
            // Число пройденных ответов
            answered: number
            // Число правильных ответов
            valid: number
        }]
 * }
 */
const DEFAULT_INITIAL_DATA = {
  isFirstTimeOpened: true,
  isMessagesAllowed: false,
  progress: [0, 0, 0, 0, 0, 0, 0],
};

export class Api {
  constructor() {}

  static baseURL = process.env.REACT_APP_API_URL;

  static __send(url) {
    return axios.get(Api.baseURL + url, {
      headers: {
        Authorization: `Bearer ${window.location.search.slice(1)}`,
      },
    });
  }

  /**
   * Проверяет ключи теста на правильность
   * @param {number[]} keys Ключи ответов
   * @returns {number} Число валидных ответов
   */
  static keysCheck(keys) {
    // @TODO: реализовать проверку ключей по тесту из JSON
  }

  /**
   * Сохраняет прогресс пользователя по тесту
   * @param {{
   *     isFirstTimeOpened: boolean;
   *  	isMessagesAllowed: boolean;
   *     progress: {
   *         id: number;
   *         answered: number;
   *         valid: number
   *     }[]
   * }} prev Локальные данные профиля
   * @param {number} quiz_id ID теста
   * @param {number[]} keys Массив чисел-ответов на тест ([1, 1, 4, 3])
   */
  async setProgress(prev, quiz_id, keys) {
    const progressItem = prev.progress.find((e) => e.id === quiz_id);
    const newProgressItem = {
      id: progressItem.id,
      answered: keys.length,
      valid: this.keysCheck(keys),
    };
    const prevProgress = progressItem
      ? prev.progress.filter((e) => e.id !== quiz_id)
      : prev.progress;

    await this.setProfile({
      ...prev,
      progress: [...prevProgress, newProgressItem],
    });
  }
  static getAllTests() {
    return this.__send(`/get_all_tests`);
  }
  static getTest(quize_id) {
    return this.__send(`/get_test?quize_id=${quize_id}`);
  }
  /**
   * Получает данные о пользователе из VK Storage
   */
  async getProfile() {
    try {
      const initialDataBridgeRequest = await bridge.send("VKWebAppStorageGet", {
        keys: ["data"],
      });

      const initialData = initialDataBridgeRequest.keys[0].value;

      return JSON.parse(initialData);
    } catch (e) {
      console.log(e);
      return DEFAULT_INITIAL_DATA;
    }
  }
  /**
   * Записывает данные о пользователе в VK Storage
   * @param {{
   *     isFirstTimeOpened: boolean;
   *     isMessagesAllowed: boolean;
   *     progress: {
   *         id: number;
   *         answered: number;
   *         valid: number
   *     }[]
   * }} data Данные профиля
   * @returns {object} { result: true } | { result: false, error: VKWebAppStorageSetFailed }
   */
  async setProfile(data) {
    const request = await bridge.send("VKWebAppStorageSet", {
      key: "data",
      value: JSON.stringify(data),
    });

    if (request.result) {
      return { result: request.result };
    } else {
      return { result: false, error: result };
    }
  }
  static getTestCount() {
    return this.__send(`/get_test_count`);
  }
  async first() {
    const data = await this.getProfile();
    return data.isFirstTimeOpened;
  }

  /**
   * Ставит значение isFirstTimeOpened в стор.
   * @param {{
   *     isFirstTimeOpened: boolean;
   *     progress: {
   *         id: number;
   *         answered: number;
   *         valid: number
   *     }[]
   * }} prev Локальные данные профиля
   * @param {boolean} isFirstTimeOpened Новое значение isFirstTimeOpened
   * @returns
   */

  async update(props) {
    const data = await this.getProfile();
    await this.setProfile({
      ...data,
      ...props,
    });
    return {
      ...data,
      ...props,
    };
  }

  async setFirst(prev, isFirstTimeOpened = false) {
    prev = await this.getProfile();
    let resp = await this.setProfile({
      ...prev,
      isFirstTimeOpened: isFirstTimeOpened,
    });
    console.log(resp);
  }
  static setReview(review) {
    return this.__send(`/set_review?review=${review}`);
  }
  static getLevel() {
    return this.__send(`/get_level`);
  }
}
