import React, { useRef, useState } from "react";
import s from "./SuperGame.module.sass";
import SharedSvgSelector from "../../shared/SvgSelector/SvgSelector";
import superGame from "../../../img/background/superGame.png";

import Header from "../../shared/Navigation/Header/Header";
import { AccentButtonWidth } from "../../UI/Buttons/ButtonWidth/ButtonWidth";
import { AccentButtonColors } from "../../UI/Buttons/AccentButton/AccentButtonColors";
import AccentButton from "../../UI/Buttons/AccentButton/AccentButton";
import bridge from "@vkontakte/vk-bridge";
import { Api } from "../../../api/api";
const MyApi = new Api();
const SuperGame = ({ id, fetchedUser, activePanel, setActivePanel }) => {
  const [value, setValue] = useState("");
  const [mailing, setMailing] = useState(false);
  const [post, setPosting] = useState(false);

  async function superGameSend() {
    if (mailing && post) {
      await MyApi.update({ complete: true });
      setActivePanel("superGameWin");
    } else {
      setActivePanel("superGameFailed");
    }
  }

  return (
    <>
      <img src={superGame} alt="" className={s.BackgroundImage} />
      <div className={s.SuperGameWrapper}>
        <Header
          backButton={true}
          go={() => {
            setActivePanel("home");
          }}
        />
        <div className={s.SuperGameContent}>
          <p className={s.Title}>Суперигра</p>
          Фух, вот и все, финишная прямая! Самое время сделать последний шаг и
          получить крутой итоговый подарок — подписку СберПрайм для молодежи на
          3 месяца за 1 руб
          <div className={s.SuperGameBlock}>
            <div className={s.Condition}>
              <div className={s.Marker}>1</div>
              <p>
                <a
                  href={"#"}
                  onClick={() => {
                    bridge
                      .send("VKWebAppShowWallPostBox", {
                        message: `Всем привет!

Я принял(а) участие во Всероссийском Практикуме по предпринимательству для молодежи!
                        
Теперь знаю основы запуска своего собственного бизнеса! 🚀

Хотите также?
Переходите на сайт проекта:
https://practicum.sberclass.ru/
                      `,
                        attachments: "https://practicum.sberclass.ru/",
                      })
                      .then((r) =>
                        r.post_id ? setPosting(true) : setPosting(false)
                      );
                  }}
                >
                  Поделись публикацией
                </a>{" "}
                о «Практикуме предпринимательства» у себя на странице ВКонтакте.
              </p>
            </div>
            <div className={s.Condition}>
              <div className={s.Marker}>2</div>
              <p>
                Подпишись на наше сообщество во ВКонтакте{" "}
                <a
                  href="#"
                  onClick={() => {
                    bridge
                      .send("VKWebAppJoinGroup", { group_id: 213754978 })
                      .then((r) => {
                        if (r.result === true) {
                          setMailing(true);
                        }
                      });
                  }}
                >
                  «Поколение Tech».
                </a>
              </p>
            </div>
            <div className={s.Condition}>
              <div className={s.Marker}>3</div>
              <p>Поделись обратной связью</p>
            </div>
            <div className={s.WrapperInput}>
              <textarea
                className={s.Input}
                value={value}
                onChange={(e) => {
                  setValue(e.target.value);
                }}
                placeholder={
                  "Расскажи, что тебе понравилось, а что могло бы быть лучше..."
                }
              />
            </div>
            <div className={s.AccentButtonWrapper}>
              <AccentButton
                onClick={superGameSend}
                backgroundColor={AccentButtonColors.PURPLE}
              >
                <p className={s.AccentButton}>Я все сделал!</p>
              </AccentButton>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default SuperGame;
